import React from 'react'
import {NavLink } from 'react-router-dom';

export default function Tabs(props) { 
  
  let tabThemeChange = {
    color: props.mode === 'dark' ? 'white' : 'black',
    backgroundColor: props.mode === 'dark'? 'transparent':''
  }
  return (
    <>    
    <main className="container text-light">
      <ul className="nav nav-tabs">
        <li className="nav-item" >
          <NavLink to="/" end className={`nav-link tab-nav`} style={tabThemeChange}>
            About
          </NavLink>
        </li>
        <li className="nav-item" id='contact'  >
          <NavLink to="/contact" className={`nav-link tab-nav`} style={tabThemeChange}>
            Contact
          </NavLink>
        </li>
        <li className="nav-item" id='feedback'>
          <NavLink to="/feedback" className={`nav-link tab-nav`} style={tabThemeChange}>
            Feedback
          </NavLink>
        </li>
      </ul>
    </main>
    
    </>
  );
}
