import React from "react";

export default function AboutContent(props) {
  return (
    <>
    <div className="mx-2">
      <p style={{color: props.mode !== 'dark'? '#666': '#ccc'}}>
        TextPlay is a website that can manipulate the text, search in the text,
        find and replace the text, count words and letters and calculate the
        time to read the text.
        <br />
        <br />
        TextPlay can be used for various purposes, such as editing a document,
        searching for a specific word or phrase, or calculating the time it
        would take to read a document.
        <br />
        <br />
        TextPlay is a convenient tool for anyone who needs to manipulate or
        search through text.
      </p>

      <h3 style={{color: props.mode !== 'dark'? '#555': '#ccc'}}>Features: </h3>
      <ul style={{color: props.mode !== 'dark'? '#666': '#ccc'}}>
        <li>Count the number of words</li>
        <li>Count the number of letters</li>
        <li>Find and replace through the text</li>
        <li>Calculate the average time to through the text</li>
        <li>Convert text to uppercase</li>
        <li>Convert text to lowercase</li>
        <li>Remove vowels from the text</li>
        <li>Reverse the text</li>
        <li>Remove extra empty spaces</li>
        <li>Remove extra empty lines</li>
        <li>Finally copy the text just by clicking</li>
      </ul>
    </div>
    </>
  );
}
