import "./App.css";
import React, { useState } from "react";
import { Routes, Route } from "react-router-dom";

import Navbar from "./components/Navbar.js";
import Tabs from "./components/Tabs.js";
import AboutContent from "./components/tabContents/AboutContent";
import ContactContent from "./components/tabContents/ContactContent";
import Feedback from "./components/tabContents/Feedback";
import Textarea from "./components/Textarea";
import Buttons from "./components/Buttons";
import Footer from "./components/Footer";

function App() {
  const logoText = "TextPlay";

  const [text, setText] = useState("");
  const handleChange = (e) => {
    setText(e.currentTarget.value);
  };

  const [mode, setMode] = useState("light");
  document.body.style.backgroundColor = mode === "dark" ? "#343a40" : "white";
  let textModeStyle = {
    color: mode === "dark" ? "white" : "#042743",
  };
  const [modeLabel, setModeLabel] = useState("Enable Dark Mode");
  const modeSwitched = () => {
    if (mode === "light") {
      setMode("dark");
      setModeLabel("Disable Dark Mode");
    } else {
      setMode("light");
      setModeLabel("Enable Dark Mode");
    }
  };

  // Handling buttons click
  const handleUpClick = () => {
    setText(text.toUpperCase());
  };
  const handleLoClick = () => {
    setText(text.toLowerCase());
  };
  const handleClearClick = () => {
    setText("");
  };
  const handleExSpClick = () => {
    setText(text.replace(/\s+/g, " "));
  };

  const [copyBtnToolTip, setCopyBtnToolTip] = useState("Copied!");
  const handleCopyClick = () => {
    if (text.length === 0) {
      setCopyBtnToolTip("Nothing to copy!");
    }else{
      navigator.clipboard.writeText(text);
      setCopyBtnToolTip("Copied!");
    }
  };
  const handleReverseClick = () => {
    setText(
      text
        .split("")
        .reverse()
        .join("")
    );
  };
  const handleRemoveVowelClick = () => {
    setText(text.replace(/[aeiou]/gi, ""));
  };

  // setting up find and replace function
  const [findText, setFindText] = useState("");
  const [replaceText, setReplaceText] = useState("");
  const findChange = (e) => {
    setFindText(e.currentTarget.value);
  };

  const highlightFindText = () => {
    let regex = new RegExp(findText, "i");
    let newText = text.replace(
      regex,
      `<span class="highlight">${findText}</span>`
    );
    let preview = document.getElementById("preview");
    preview.innerHTML = newText;
    preview.scrollIntoView();
  };
  const replaceChange = (e) => {
    setReplaceText(e.currentTarget.value);
  };

  const handleFindClick = (e) => {
    let find = findText;
    let replace = replaceText;
    let regex =
      e.currentTarget.textContent === "Replace All"
        ? new RegExp(find, "gi")
        : new RegExp(find, "i");
    let newText = text.replace(regex, replace);
    setText(newText);
  };

  return (
    <>
      <Navbar
        logoText={logoText}
        mode={mode}
        modeLabel={modeLabel}
        modeSwitched={modeSwitched}
      />
      <Tabs textModeStyle={textModeStyle} mode={mode} />

      <div className="container my-3 mb-5">
        <Routes>
          <Route path="/" element={<AboutContent mode={mode} />} />
          <Route path="/home" element={<AboutContent mode={mode} />} />
          <Route path="/about" element={<AboutContent mode={mode} />} />
          <Route path="/contact" element={<ContactContent mode={mode} />} />
          <Route path="/feedback" element={<Feedback mode={mode} />} />
        </Routes>
      </div>

      <div className="container">
        <hr />
        <h3 style={textModeStyle}>Enter your text here:</h3>
        <Textarea text={text} handleChange={handleChange} />

        <div
          className="alert alert-warning alert-dismissible fade show my-2"
          role="alert"
        >
          <strong>Sorry! </strong>for your inconvenience: <b>Copy</b> button may
          not work in some android devices. It will be fixed soon.
          <button
            type="button"
            className="btn-close"
            data-bs-dismiss="alert"
            aria-label="Close"
          ></button>
        </div>
        <Buttons
          mode={mode}
          handleUpClick={handleUpClick}
          handleLoClick={handleLoClick}
          handleClearClick={handleClearClick}
          handleExSpClick={handleExSpClick}
          handleCopyClick={handleCopyClick}
          copyBtnToolTip={copyBtnToolTip}
          textToCopy={text}
          handleReverseClick={handleReverseClick}
          handleRemoveVowelClick={handleRemoveVowelClick}
          findText={findText}
          findChange={findChange}
          replaceText={replaceText}
          replaceChange={replaceChange}
          handleReplaceClick={handleFindClick}
          highlightFindText={highlightFindText}
        />

        <h4 className="my-3" style={textModeStyle}>
          <strong>Text Summary</strong>
        </h4>
        <p style={textModeStyle}>
          {
            text.split(/\s+/).filter((string) => {
              return string.length !== 0;
            }).length
          }{" "}
          <b>words</b> and {text.split("").length}{" "}
          <b>characters including white spaces</b> and{" "}
          {
            text.split("").filter((letter) => {
              return letter !== " ";
            }).length
          }{" "}
          <b>without white spaces</b>
        </p>
        <p style={textModeStyle}>
          <b>Time to read:</b>{" "}
          {0.00066 *
            text.split("").filter((letter) => {
              return letter !== " ";
            }).length}{" "}
          minutes{" "}
        </p>
        <h5 style={textModeStyle}>Preview</h5>
        <p id="preview" style={textModeStyle}>
          {text.length === 0 ? `Nothing to show here!` : text}
        </p>
      </div>

      <Footer mode={mode} />
    </>
  );
}

export default App;
