import React from 'react'

export default function Footer(props) {
    let footerStyle = {
        width: '100%',
        height: '50px',
        backgroundColor: props.mode === 'dark' ? '#343a40 !important' : 'grey !important',
        color: props.mode === 'dark' ? 'white' : '#042743',
        margin: '0',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'

    }
    return(
        <>
        <footer className={`footer bg-${props.mode} text-${props.mode === 'dark' ? 'light' : 'dark'}`}style={footerStyle}>
            <div className="container" style={{textAlign: 'center'}}>
                <span className="text-muted">© 2022 TextPlay. All Rights Reserved | Terms and Conditions</span>
            </div>
        </footer>
        </>
    )
}