import React, { useRef } from "react";

export default function Buttons(props) {
  let btnStyle = {
    fontSize: "12px",
    color: props.mode === "dark" ? "#252525" : "#fefefe",
    backgroundColor: props.mode === "dark" ? "#fefefe" : "#252525",
  };

  const copyBtnRef = useRef();
  let copyBtnClicked = () => {
    props.handleCopyClick();
      copyBtnRef.current.classList.add("copyBtn");
    
    setTimeout(() => {
      copyBtnRef.current.classList.remove("copyBtn");
    }, 3000);
  };

  return (
    <>
      <div>
        <button
          ref={copyBtnRef}
          type="button"
          className={`btn btn-outline-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={copyBtnClicked}
          tool-tip={props.copyBtnToolTip}
        >
          Copy Text
        </button>
        <button
          type="button"
          className={`btn btn-outline-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={props.handleClearClick}
        >
          Clear Text
        </button>
        {/* Button trigger modal  */}
        <button
          type="button"
          className={`btn btn-outline-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
        >
          Find and Replace
        </button>
      </div>

      <div>
        <button
          type="button"
          className={`btn btn-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={props.handleUpClick}
          style={btnStyle}
        >
          Uppercase
        </button>
        <button
          type="button"
          className={`btn btn-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={props.handleLoClick}
          style={btnStyle}
        >
          Lowercase
        </button>
        <button
          type="button"
          className={`btn btn-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={props.handleExSpClick}
          style={btnStyle}
        >
          Remove extra spaces
        </button>
        <button
          type="button"
          className={`btn btn-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={props.handleReverseClick}
          style={btnStyle}
        >
          Reverse text
        </button>
        <button
          type="button"
          className={`btn btn-${
            props.mode === "dark" ? "light" : "dark"
          } my-2 mx-1`}
          onClick={props.handleRemoveVowelClick}
          style={btnStyle}
        >
          Remove vowels
        </button>

        {/* Modal */}
        <div
          className="modal fade"
          id="exampleModal"
          tabIndex="-1"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h1 className="modal-title fs-5" id="exampleModalLabel">
                  Find and Replace
                </h1>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                  title="Close"
                ></button>
              </div>
              <div className="modal-body">
                <div className="form-floating mb-3">
                  <input
                    type="email"
                    className="form-control"
                    id="floatingInput"
                    placeholder="Find"
                    value={props.findText}
                    onChange={props.findChange}
                  />
                  <label htmlFor="floatingInput">Find</label>
                </div>
                <div className="form-floating mb-3">
                  <input
                    type="email"
                    className="form-control"
                    id="floatingInput"
                    placeholder="Replace with"
                    value={props.replaceText}
                    onChange={props.replaceChange}
                  />
                  <label htmlFor="floatingInput">Replace with</label>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-outline-secondary"
                  data-bs-dismiss="modal"
                >
                  Close
                </button>
                <button
                  type="button"
                  className="btn btn-dark"
                  onClick={props.handleReplaceClick}
                  disabled={
                    props.replaceText.length === 0 ||
                    props.findText.length === 0
                  }
                  data-bs-dismiss="modal"
                >
                  Replace
                </button>
                <button
                  type="button"
                  className="btn btn-dark"
                  onClick={props.handleReplaceClick}
                  disabled={
                    props.replaceText.length === 0 ||
                    props.findText.length === 0
                  }
                  data-bs-dismiss="modal"
                >
                  Replace All
                </button>
                <button
                  type="button"
                  className="btn btn-dark"
                  onClick={props.highlightFindText}
                  disabled={props.findText.length === 0}
                  data-bs-dismiss="modal"
                >
                  Find
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
