import React from "react";
import { NavLink } from "react-router-dom";

export default function Navbar(props) {
    let colorChange = {
        color: props.mode === 'dark' ? 'white' : '#042743'
    }
    let backgroundColorChange = {
        backgroundColor: props.mode !== 'dark'? '#e9ecef': '#343a40'
    }
  return (
    <div>
      <nav className={`navbar navbar-${props.mode} bg-${props.mode} fixed-top`}>
        <div className="container-fluid">
          <a className="navbar-brand" href="/" style={colorChange}>
            {props.logoText}
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="offcanvas"
            data-bs-target="#offcanvasDarkNavbar"
            aria-controls="offcanvasDarkNavbar"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div
            className="offcanvas offcanvas-end text-bg-dark"
            tabIndex="-1"
            id="offcanvasDarkNavbar"
            aria-labelledby="offcanvasDarkNavbarLabel"
          >
            <div className="offcanvas-header" style={backgroundColorChange}>
              <h5 className="offcanvas-title" id="offcanvasDarkNavbarLabel" style={colorChange}>
                {props.logoText}
              </h5>
              <button
                type="button"
                className={`btn-close btn-close-${props.mode === 'dark' ? 'white' : 'dark'}`}
                data-bs-dismiss="offcanvas"
                aria-label="Close"
              ></button>
            </div>
            <div className="offcanvas-body" style={backgroundColorChange}>
              <ul className="navbar-nav justify-content-end flex-grow-1 pe-3">
                <li className="nav-item">
                  <NavLink to="/" end className="nav-link" style={colorChange}>
                    About
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to="/contact" end className="nav-link" style={colorChange}>
                    Contact
                  </NavLink>
                </li>
                <li className="nav-item">
                  <div className="form-check form-switch">
                    <label className="form-check-label" htmlFor="flexSwitchCheckDefault" style={colorChange}>{props.modeLabel}</label>
                    <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick={props.modeSwitched}/>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}
