import React from "react";

export default function Feedback(props) {
  let style = {
    color: props.mode !== 'dark'? '#555':'#ccc'
  }
  return (
    <>
      <div className="container px-2">
        <form className="row g-3" action="https://formspree.io/f/mnqwwndo" method="POST">
          <div className="col-md-6">
            <label for="inputName" className="form-label" style={style}>
              Name:<span style={{color: 'red'}}>*</span>
            </label>
            <input type="text" className="form-control" id="inputName" name="Name" required minLength="3" />
          </div>
          <div className="col-md-6">
            <label for="inputEmail4" className="form-label" style={style}>
              Email
            </label>
            <input type="email" className="form-control" id="inputEmail4" name="E-mail" />
          </div>
          <div className="col-md-12">
            <label for="inputSub" className="form-label" style={style}>
              Subject
            </label>
            <input type="text" className="form-control" id="inputSub" name="Subject"/>
          </div>
          <div className="col-md-12">
            <label for="inputMsg" className="form-label" style={style}>
              Message:<span style={{color: 'red'}}>*</span>
            </label>
            <textarea id="inputMsg" className="form-control" name="Message" rows="4" required minLength="20"></textarea>
          </div>
          <div className="col-12">
            <button type="submit" className={`btn btn-${props.mode === 'dark'? 'light':'dark'}`}>
              Send
            </button>
          </div>
        </form>
      </div>
    </>
  );
}
